	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Christopher is home","date":"02 Jan 2000","slug":"/2000-01-02-christopher-is-home/","tags":"Y2000"},"html":"<p><b>03 Jan 7:45 am CET</b> Our first night together. Christopher is sleeping 5 hours at a stretch, so we were up only once. We're pleasantly surprised.</p>\n<p>Christopher came about 4 pm today. We're using the basket from the baby carriage as his first bed. We can then carry him up or down the stairs to whatever room we're in. In this picture he's under the Christmas tree.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crhxmas.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"crhxmas.jpg: \" /></p>\n<p>Looking at Christopher, I have the feeling I am looking in a mirror. To confirm the resemblence, here is a picture of me from  February 1962 (scanned Polaroid b&w snapshot) and of Christopher from his first day (digital photo converted to b&w)</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/smh.jpg\" height=\"337\" width=\"400\" border=\"0\" alt=\"smh.jpg: \" /></p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh_sw.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"crh_sw.jpg: \" /></p>"},"next":{"fields":{"title":"Christopher Ryan Hanson","date":"01 Jan 2000","slug":"/2000-01-01-christopher-ryan-hanson/"}},"previous":{"fields":{"title":"Happy Birthday Steve","date":"03 Jan 2000","slug":"/2000-01-03-happy-birthday-steve/"}}},{"node":{"fields":{"title":"Christopher Ryan Hanson","date":"01 Jan 2000","slug":"/2000-01-01-christopher-ryan-hanson/","tags":"Y2000"},"html":"<p>Good news! Mama's fever is down, she and Christopher are feeling fine. If his bilirubin value keeps sinking, they can come home tomorrow or Monday. We'll know for sure around noon tomorrow.</p>\n<p>We got a nice New Year's present from the German government today... a gas tax increase of 7 Pf went into effect, and the oil companies were kind enough to add 4 Pf of their own. I filled up for DM 1,90 today. That's over a dollar per liter... over $4 per gallon.</p>"},"next":{"fields":{"title":"Christopher Ryan Hanson","date":"31 Dec 1999","slug":"/1999-12-31-christopher-ryan-hanson/"}},"previous":{"fields":{"title":"Christopher is home","date":"02 Jan 2000","slug":"/2000-01-02-christopher-is-home/"}}},{"node":{"fields":{"title":"Christopher Ryan Hanson","date":"31 Dec 1999","slug":"/1999-12-31-christopher-ryan-hanson/","tags":"Y1999"},"html":"<p><b>1 Jan 2000 2:20 am CET:</b> The lights are still on and we are still here! (the EditThisPage server is located in California, so this page is still date 1999 for now)</p>\n<p>I celebrated the new millenium at Mama's bedside at the hospital. She came down with a fever at 11pm, so we couldn't even drink the champange I had smuggled in. All we could do at midnight was smile and kiss. Christopher slept through it all. </p>\n<p>Christopher's jaundice had gone down, so he was in the room with us, not under the UV lights. Nursing went well today, too. </p>\n<p>I now have a real name for this website! We've been calling each other 'PapaScott' and 'MamaMaus' for several months now, so it seems to fit. I hope the font for the logo isn't <i>too</i> dignified.</p>\n<p>My mother asked why vacuum babies are more prone to high bilirubin. The nurse explained that the bruising caused by the vacuum is stress on circulation and hence on the liver.</p>\n<p>I think I found a better place to keep baby pictures at <a href=\"http://albums.photopoint.com/j/AlbumList?u=185392\">PhotoPoint</a>.</p>"},"next":{"fields":{"title":"Christopher Ryan Hanson","date":"30 Dec 1999","slug":"/1999-12-30-christopher-ryan-hanson/"}},"previous":{"fields":{"title":"Christopher Ryan Hanson","date":"01 Jan 2000","slug":"/2000-01-01-christopher-ryan-hanson/"}}}],"pathPrefix":"","first":false,"last":false,"index":1094,"pageCount":1101}}

